.popup {
  background-color: rgba(0, 0, 0, 0.5);
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 1;
  visibility: hidden;
  opacity: 0;
  transition: 0.3s linear;
  width: 100%;
  height: 100%;
  justify-content: center;
  align-items: center;
  display: flex;
}
